import React, { useState } from 'react';
import PropTypes from 'prop-types';
// Design Resources
import { Button, Slider } from 'antd';
// Components
import Title from '../../shared/Title';
import Instruction from '../../shared/Instruction';
import Dial from './Dial';
import AvatarName from '../../avatars/AvatarName';

function DialGuessSelection({
  activeTeam,
  teamController,
  amITheController,
  isMyTeamActive,
  onSendGuess,
  card,
}) {
  const [needle, setNeedle] = useState(0);

  return (
    <div className="o-dial-guess-selection">
      <Title>Time {activeTeam}, hora de brilhar!</Title>
      <Instruction contained>
        Qual nmero melhor indica <span className="o-dial-guess-selection__clue">{card.clue}</span> na escala
        de{' '}
        <strong>
          {card.left}-{card.right}
        </strong>
        ?
      </Instruction>

      <Dial card={card} showTarget={false} target={card.target} showNeedle={isMyTeamActive} needle={needle} />

      {isMyTeamActive ? (
        <Instruction contained>
          Discuta com seu time em qual nmero do medidor de ondas telepticas voc acha que a dica se encaixa
          melhor. Se voc  o psquico dessa rodada, boca fechada!
          <br />
          Todos podem controlar o ponteiro, mas somente{' '}
          {amITheController ? 'VOC ' : <AvatarName player={teamController} />}pode apertar o boto para
          enviar a resposta final.
        </Instruction>
      ) : (
        <Instruction contained>
          O outro time agora est tentando adivinhar onde no medidor de ondas telepticas a dica se encaixa
          melhor. Na prxima fase, voc e seu time tero a change de contra-atacar dizendo se a soluo est
          mais para esquerda ou mais para a direita do que eles escolheram!
        </Instruction>
      )}

      {isMyTeamActive && (
        <div>
          <Slider defaultValue={0} min={-10} max={10} onChange={setNeedle} />
          <div className="container container--transparent container--center">
            <Button
              type="primary"
              onClick={() => {
                onSendGuess({ guess: needle });
              }}
              disabled={!amITheController}
            >
              Enviar resposta: {needle}
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}

DialGuessSelection.propTypes = {
  onSendGuess: PropTypes.func.isRequired,
  card: PropTypes.shape({
    id: PropTypes.string,
    left: PropTypes.string,
    right: PropTypes.string,
  }).isRequired,
  activeTeam: PropTypes.oneOf(['A', 'B']),
  teamController: PropTypes.object,
  amITheController: PropTypes.bool,
  isMyTeamActive: PropTypes.bool,
};

export default DialGuessSelection;
